.itd-main-ctn{
    width: 100%;
    padding: 0 40px;
}
.itd-int-ctn {
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.itd-img {
    width:300px;
    height: 320px;
    border-radius: 10px;
}
.itd-infos {
    width: 400px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap:30px;
}

.itd-type {
    font-size: 13px;
    font-weight: 500;
    color:#2f2a2a;
}
.itd-type-limited {
    font-size: 13px;
    font-weight: 500;
    color:#c11b20 ;
} 
.itd-type-spec {
    font-size: 13px;
    font-weight: 500;
    color:#b7299d ;
} 
.itdbctn {
    width: 200px;
    
}
.itd-img-desc {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin-right: 170px;
}

.itdn-ctn {
    display: flex;
    flex-direction: column;
    gap:2px;
    width: fit-content;
}
.itd-line {
    width:56%;
    height: 2px;

}
@media (max-width:990px) {
    .itd-int-ctn {
        width:100%;
        display: flex;
        flex-direction: column;
        align-items:center;
        gap:20px
    }

    .itd-infos {
        width: 100%;
        
    }
    
.itd-img-desc {
    margin-right: 0px;
    margin-top: 30px;
}
}
@media (max-width: 768px) {
    .itd-infos {
     
        align-items: center;
        gap:30px;
    
    }
}

@media (max-width:480px) {
    .itd-main-ctn{
        padding: 0 20px;
    }
    .itd-img {
        width:100%;
        max-height: 400px;
    }
    .itdn {
font-size: 18px;      
  }
  .itdbctn {
    width: 100%;
}
.itd-img-desc {
    /* display: none; */
}
}