.ch-ctn {
    width:100%;
    display: flex;
    justify-content: center;
    margin-top: 100px;
}
.ch-input-ctn {
    width:55%;
    height: fit-content;
    background-color: #fffefc;
    border-top-right-radius: 14%;
    padding:18px 40px;
    padding-bottom: 80px;
}
.ch-paper-ctn {
    width:45%;
    height: fit-content;
    padding:18px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}
.ch-inp-w {
    width: 100%;
}
ul {list-style-type: disc;}
.ch-in-pay-title {
    font-size: 14px;
    font-weight: 400;
    color:#656565;
}
.ch-pay-input {
    width:fit-content;
    height: fit-content;
    padding: 2px 0;
    margin: 20px 0;
    display: block;
}
.ch-in-pay {
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:15px;
    margin-top: 20px;
}
.pay {
    width:47%;
    height: 50px;
    border-radius: 3px;
}
.pay1  {
    background-color: #ffff00;
}
.pay2 {
    background-color: #bcbcff;
}
.pay1:hover  {
    background-color: #d6d607;
}
.pay2:hover {
    background-color: #9898ff;
}
.ch-in-pay-desc {
    border-radius: 15px;
    padding-left: 10px;
    margin-top: 20px;
    height: 100px;
    position: relative;
    overflow: hidden;
}
.pay-layer1 {
position: absolute;
transform: rotate(45deg);
top:-80px;
left:-50px;
width: 100px;
height: 200px;
background-color: #ffff00;
}
.pay-layer2 {
    position: absolute;
    transform: rotate(45deg);
    top:-80px;
    left:-50px;
    width: 100px;
    height: 200px;
    background-color: #bcbcff;
    }
.ch-del-ctn {
    display: flex;
    flex-direction: column;
    gap:20px;
}
.ch-double-inp-ctn {
    width: 100%;
    display: flex;
    align-items: center;
    gap:10px;
    justify-content: space-between;
}
.ch-add-inp {
    width:43%;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


.ch-item-ctn {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    gap:20px;
    width: 100%;
}
.ch-item {
    display: flex;
    width:100%;
    justify-content: space-between;
    align-items: center;
}
.ch-item1 {
    display: flex;
    align-items: center;
    gap:15px;
    width: 70%;
}
.ch-itm-img {
    width:80px;
    height: 80px;
    border-radius: 3px;
}
.ch-total-ctn {
    margin-top: 20px;
    display: flex;
    width:100%;
    justify-content: space-between;
    align-items: center;
}

.ch-rec-ctn {
    width:80%;
    z-index: 5;
    height: fit-content;
    position: fixed;
    top: 0;
    right:0;
    left:0;
    bottom: 0;
    margin:auto;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    gap:15px;
    padding:20px 25px;
}
.ch-rec-title {
    font-size: 20px;
    font-weight: 500;
}
.ch-rec-n {
    
}
.ch-rec-img {
    width:100px;
    height:200px;
}
@media (max-width:768px) {
    .ch-ctn {
        flex-direction: column-reverse;
    }
    .ch-input-ctn {
        width:100%;

    }
    .ch-paper-ctn {
        width:100%;
    
    }
    .ch-double-inp-ctn {
        flex-direction: column;
        }
    .ch-add-inp {
        width:100%;
    }
}

@media (max-width:480px) {
    .ch-input-ctn {
        padding:18px 20px;
    }
    .ch-paper-ctn {
        padding:18px 20px;

    }
    .ul {
        margin-left: 10px;
    }
    .ch-in-pay {
       
        gap:5px;
    }
    #ch-pay {
        width:80%;
    }
    
.ch-rec-ctn {
    padding:20px 15px;
}
}