.ct-main-ctn {
    padding: 0 50px;
}
.ct-title {
    margin-bottom: 100px;
}
.ct-desc {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    width:100%;
    border-bottom: 1px solid #6c6c6c;
}
.ct-pn {
    width:45%;
}
.ct-pp {
}
.ct-pq {
}
.ct-pc {
}
.ct-prod-ctn {
    margin-top: 50px;
    width:100%;
}
.pro1 {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    width:100%;
    align-items: center;
    
}
.pro-n-ctn {
    width:45%;
    display: flex;
    align-items: center;
    gap:25px;
}
.pro-img {
    width:150px;
    height: 150px;
    border-radius: 4px;
}
.pro-titles-ctn {
    display: flex;
    flex-direction: column;
    gap:18px;

}
.pro-img:hover , .pro-titles-ctn:hover , .pro2-img:hover , .pro2-title-ctn:hover {
    opacity: .6;
}
.pro-type {
    color:#575656;
}
.pro-q-ctn {
    display: flex;
    align-items: center;
    gap:15px;
}
.pro-q-no {
    padding: 10px;
    background: rgb(255, 255, 255);
    border-radius: 3px;
}
.pro2 {
    position: relative;
    width:100%;
    margin: 0 auto;
    padding-top: 80px;
    border: 1px solid #797676;
    border-radius: 10px;
    display: none;
    flex-direction: column;
    align-items: center;
    gap:30px;
    margin-bottom: 40px;
}
.pro2-img {
    width:200px;
    margin: 0 auto;
    height: 200px;
    border-radius: 3px;
}
.ct-setting-ctn {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap:15px;
}
.ct-btn {
    width:240px;
    background-color: red;
}
@media (max-width: 990px) {
    .ct-pn {
        width:49%;
    }
}
@media (max-width: 768px) {
    .ct-desc {
        display: none;
      
    }
    .pro1 {
        display: none;
    }
    .pro2{
        display: flex;
    }
    .pro-titles-ctn {
        display: flex;
        flex-direction: column;
        align-items: center;  
         gap:5px;
        margin-top: 10px;;
    }
    .pro-q-ctn {
        margin-top: -50px;
    }
    .pro-c-ctn {
        margin-top: -50px;
    }
}

@media (max-width:480px) {
    .ct-main-ctn {
        padding: 0 10px;
    }
    .pro-titles-ctn {
text-align: center;
    }
    .ct-setting-ctn {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap:15px;
    }
    .ct-btn {
        width:100%;
    }

}