.nav-ctn {
    width:100%;
    position: fixed;
    top:0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:20px 70px;
    z-index: 5;
}
.nav-list-ctn {
    display: flex;
    gap:45px;
    align-items: center;
}
.nav-bar-ctn{
    display: none;
}
.cancel {
    transform: rotate(45deg);
}
.menu-page-andriod-cl {
    display: flex;
    position: fixed;
    top:0px;
    width: 100%;
    height: 0;
}
.menu-page-andriod {
    display: flex;
    position: fixed;
    top:1px;
    width: 100%;
    height: 100vh;
    z-index: 6;
}
.nav-pg-cl {
    height: 0;
    background: #fefffe;

    width:20%;
}
.nav-pg {
    height: 100%;
    width:20%;
    background: #fefffe;
}
.pg-1{
    transition-delay: 0s;
}
.pg-2{
    transition-delay: .1s;
}
.pg-3{
    transition-delay: .2s;
}
.pg-4{
    transition-delay: .3s;
}
.pg-5{
    transition-delay: .4s;
}

.nav-list:hover {
    color:#08bb55;
}
.menu-page-list{
    position: absolute;
    width:100%;
    height: 100%;
}
.menu-page-routes {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:34px;
    width:fit-content;
    height: fit-content;
    bottom: 0;
    left:0;
    right:0;
    margin: auto;
    position: absolute;
}
.srh-page {
    width:100%;
    height: 80px;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:100px;
    z-index: 6;
}
.nav-srh-input {
    width: 230px;
    height: 30px;
    padding: 14px 14px;
    border-bottom: 1px solid #343131;
    background-color: inherit;
}
.nav-srh-input:focus{
    outline: inherit;
}
@media (max-width: 990px) {
    /* .menu-page-andriod {
        display: flex;
        position: fixed;
        top:1px;
        width: 100%;
        height: 100vh;
    } */
    .nav-title {
        font-size: 21px;
        display: flex;
        align-items: center;
        gap:20px;
    }
    .nav-list-ctn {
        display: none;
     
    }

    .nav-bar-ctn{
        display:block;

    }
    
}
@media (max-width: 768px) {
    .nav-ctn {
        padding:20px 50px;

        }
}
@media (max-width: 480px) {
    .nav-ctn {
        padding:15px 20px;

        }
        .nav-title {
            font-size: 18px;
            gap:18px;
        }
        .srh-page {
            gap:50px;
        }
        .nav-srh-input {
            width: 180px;
   
        }
}