.abt-main {
    width:100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:20px;
}
.abt-ctn {
    width: 500px;
    height: 270px;
    background-color: #d8fbcd;
    position: relative;
    border-radius: 20px;
}
.abt-text{
    width:100%;
    height: 100%;;
    position: absolute;
    bottom:-50px;
    right: -50px;
    text-align: justify;
}

@media (max-width: 768px) {
    .abt-ctn {
        width: 70%;
        height: 300px;
        margin-left: 0;
    }
    .abt-text{
        bottom:-40px;
        right: -40px;
    }
    
}
@media (max-width: 480px) {
    .abt-ctn {
        width: 90%;
        height: 350px;
        margin-left: -50px;
    }
    .abt-text{
        bottom:-30px;
        right: -30px;
    }
    
}