.sm-main-ctn {
    background-color: #fff9f9;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 260px;
}
.sm-img-ctn {
    transform: rotate(55deg);
    position: absolute;
    width:600px;
    height: 390px;
    overflow: hidden;
    left: -105px;
    top:0;
}
.sm-img {
    width:100%;
    height: 100%;;
    transform: rotate(-55deg);
    opacity: .9;
}
.sm-title-ctn {
    width:42%;
    height: 100%;
    position: absolute;
    right:0;
    top:0;
    bottom:0;
    margin: auto;
    flex-direction: column;
    gap:20px;
    display: flex;
    justify-content: center;
    padding-right: 40px;
}

@media (max-width:990px) {
    .sm-img-ctn {
        width:570px;
        height: 380px;
        left: -185px;
    }
    .sm-title-ctn {
        width:50%;
     ;
    }
}
@media (max-width:768px) {
    .sm-img-ctn {
        width:560px;
        height: 380px;
        left: -280px;
        top:0px;
    }
    .sm-img{
        opacity: .5;
    }
    .sm-title-ctn {
        width:70%;
        left:0;right:0;bottom:0;
        align-items: center;
        padding-right: 0;
    }
    .sm-desc {
        text-align: center;
    }
}
@media (max-width:480px) {
    .sm-img-ctn {
        width:100%;
        height: 100%;
        left: 0;
        top:0px;
        transform: rotate(0);
    }
    .sm-img {
        opacity: .4;
        transform: rotate(0);
    }
    .sm-title-ctn {
        width:91%;
    }
    .sm-desc {
        font-size: 21px;
    }
}