.sho-nav-ctn {
    width:200px;
    height: fit-content;
    padding: 10px 0;
    position: fixed;
    right: 0;
    left:0 ;
    margin:auto;
    top:90px;
    z-index: 5;
}
.sho-nav-ctn:hover {
    color:#08bb55;
}
.sho-cate-div {
    width:100%;
    z-index: 5;
    height: 100vh;
    position: fixed;
    top:0;
    right: 0;
    left:0 ;
    bottom:0;
    margin:auto;
}
.sho-cate {
    width:200px;
    height: fit-content;
    z-index: 6;
    background-color: white;
    position: fixed;
    top:90px;
    right: 0;
    left:0 ;
    margin:auto;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    padding: 20px;
}
.sho-navs {
    font-size: 14px;

}
.sho-navs:hover {
    color:#08bb55;
}
.sho-item-ctn {
    width:100%;
    margin: 0 auto;
}
.sho-label-ctn {
    width:460px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap:20px;
    align-items: center;
}
.sho-item-tit {
font-size: 13px;
color: #df2c32 ;
font-family: 'Poppins', sans-serif;
}
.sho-item-des {
    font-size: 28px;
    color:#3f342f;
    text-align: center;
}

@media (max-width:480px) {
    .sho-label-ctn {
        width:80%;
        gap:15px
    }
    .sho-item-des {
        font-size: 21px;
    }
    
}