.hm-main-ctn {
    padding: 0 40px;
}
.hm-intro-ctn {
    display: flex;
    width:100%;
    height: 90vh;
    align-items: center;
    justify-content: space-between;
}
.hm-intro-img-ctn {
    width:370px;
    height: 340px;
    
}
.hm-intro-btn-ctn{
    width: 50%;
    height: fit-content;
    position: relative;
    display: flex;
    flex-direction: column;
    gap:20px;
}
.hm-intro-img {
    width:100%;
    height: 100%;
    position: absolute;
    bottom: -80px;
    right: -80px;
    object-position: top;

}
.hm-intro-title-ctn {
    width:fit-content;
    font-size: 30px;
    font-weight: 500;

}
.hm-intro-new {
    width: 100%;
    margin-top: 20px;
    
}
.item-title {
    font-size: 20px;
    font-weight: 600;
    position: relative;
    width: 180px;
    height: 40px;
    padding: 5px 13px;
    margin-bottom: 20px;
    text-align: center;
}
.itm-title-layer {
    position: absolute;
    width:25px;
    height: 25px;
    top:-1px;
    left: -1px;
    border-radius: 50%;
}

@media (max-width: 990px) {
    .hm-intro-btn-ctn{
        width: 40%;
    
    }
    .hm-intro-img-ctn {
        width:340px;
        height: 310px;
        
    }
    .hm-intro-img {
     
        bottom: -60px;
        right: -60px;
    
    }
}
@media (max-width: 768px) {
    .hm-intro-ctn {
        height: fit-content;
        flex-direction: column-reverse;
        width:100%;
        align-items: center;
        gap:80px;
    }
    .hm-intro-btn-ctn{
        width: 100%;
       align-items: center;
        flex-direction: column;
    }
    .hm-intro-ctn {
        justify-content: flex-start;
    }
    .hm-intro-img-ctn {
        width:60%; 
        margin-right: 80px; 
        margin-bottom: 100px;      
    }
 .hm-intro-title-ctn {text-align: center; line-height: 40px;}
    .hm-intro-img {
        bottom: -80px;
        right: -80px;
    
    }
    
    
}
@media (max-width: 480px) {
    .hm-intro-ctn {
        height: 120vh;
    }
    .hm-main-ctn {
        padding: 0 20px;
    }
    .hm-intro-img-ctn {
        width:66%; 
        margin-right: 80px; 
        margin-bottom: 100px;      
    }
    .hm-intro-img {
        bottom: -70px;
        right: -70px;
    
    }
}